@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root {
	--primary: #efb6b2;
	--secondary: #4e4e4e;
	--error: #ff4a4a;
}

/* base styles & title */
body {
	font-family: 'Noto Serif';
	color: #4e4e4e;
	color: var(--secondary);
}
.App {
	max-width: 80%;
	margin: 0 auto;
}
.title h1 {
	color: #efb6b2;
	color: var(--primary);
	font-size: 1.2rem;
	letter-spacing: 2px;
	font-weight: normal;
}
.title h2,
.title p {
	text-align: center;
}
.title h2 {
	margin-top: 60px;
	font-size: 2.6rem;
}

/* upload form styles */
form {
	margin: 30px auto 10px;
	text-align: center;
}
label input {
	height: 0;
	width: 0;
	opacity: 0;
}
label {
	cursor: pointer;
	display: block;
	width: 90px;
	height: 30px;
	border: 1px solid #efb6b2;
	border: 1px solid var(--primary);
	border-radius: 5px;
	margin: 10px auto;
	line-height: 30px;
	color: #efb6b2;
	color: var(--primary);
	font-weight: bold;
	font-size: 24px;
}

label span {
	text-align: center;
	font-size: 18px;
}

label:hover {
	background: #efb6b2;
	background: var(--primary);
	color: white;
}
.output {
	height: 60px;
	font-size: 0.8rem;
}
.error {
	color: #ff4a4a;
	color: var(--error);
}

/* progress bar styles */
.progress-bar {
	height: 5px;
	background: #efb6b2;
	background: var(--primary);
	margin-top: 20px;
}

/* image grid styles */
.img-grid {
	margin: 20px auto;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-gap: 40px;
}
.img-wrap {
	overflow: hidden;
	height: 0;
	padding: 50% 0;
	/* padding controls height, will always be perfectly square regardless of width */
	position: relative;
	opacity: 0.8;
	border: 10px solid rgb(235, 235, 235);
	box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5), inset 0 0 5px 1px rgba(0, 0, 0, 0.5);
}
.img-wrap img {
	min-width: 100%;
	min-height: 100%;
	max-width: 150%;
	position: absolute;
	top: 0;
	left: 0;
}

/* modal styles */
.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
}
.backdrop img {
	/* background: rgba(0, 0, 0, 0.6); */
	display: block;
	max-width: 60%;
	max-height: 80%;
	margin: 60px auto;
	box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
	border: 5px solid white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

